//拓展出来的按钮不应多于6个，否则违反了作为悬浮按钮的快速、高效的原则
export default {
	name: "tuiFab",
	props: {
		//rpx 为0时值为auto
		left: {
			type: Number,
			default: 0
		},
		//rpx 当为0时且left不为0，值为auto
		right: {
			type: Number,
			default: 80
		},
		//rpx bottom值
		bottom: {
			type: Number,
			default: 100
		},
		//默认按钮 宽度 rpx
		width: {
			type: Number,
			default: 108
		},
		//默认按钮 高度 rpx
		height: {
			type: Number,
			default: 108
		},
		//圆角值
		radius: {
			type: String,
			default: "50%"
		},
		//默认按钮背景颜色
		bgColor: {
			type: String,
			default: "#5677fc"
		},
		//字体颜色
		color: {
			type: String,
			default: "#fff"
		},
		//拓展按钮
		// bgColor: "#5677fc",
		// //图标/图片地址
		// imgUrl: "/static/images/fab/fab_reward.png",
		// //图片高度 rpx
		// imgHeight: 60,
		// //图片宽度 rpx
		// imgWidth: 60,
		// //名称
		// text: "名称",
		// //字体大小
		// fontSize: 30,
		// //字体颜色
		// color: "#fff"
		btnList: {
			type: Array,
			default () {
				return []
			}
		},
		//点击遮罩 是否可关闭
		maskClosable: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			isOpen: false,
			hidden: true,
			timer: null
		};
	},
	methods: {
		getLeft() {
			let val = "auto"
			if (this.left && !this.right) {
				val = this.left + 'rpx'
			}
			return val
		},
		getRight() {
			let val = this.right + 'rpx'
			if (this.left && !this.right) {
				val = "auto"
			}
			return val
		},
		handleClick: function(index) {
			this.hidden = false
			clearTimeout(this.timer)
			if (index == -1 && this.btnList.length) {
				this.isOpen = !this.isOpen
			} else {
				this.$emit("click", {
					index: index
				})
				this.isOpen = false
			}
			if (!this.isOpen) {
				this.timer = setTimeout(() => {
					this.hidden = true
				}, 200)
			}
		},
		handleClickCancel: function() {
			if (!this.maskClosable) return;
			this.isOpen = false
		}
	},
	beforeDestroy() {
		clearTimeout(this.timer)
		this.timer = null
	}
}
